export const FACTIONS = [
  "√ÅGUIA DOURADA",
  "SERPENTE NEGRA",
  "LOBO DE FERRO",
  "CORVO ESCARLATE"
] as const;

export type FactionName = (typeof FACTIONS)[number];
export type FactionWarTeams = { teamA: FactionName[]; teamB: FactionName[] };
export type FactionWarState = FactionWarTeams & {
  startedAt: string;
  remaining: Record<FactionName, number>;
};

const FACTION_CHANTS: Record<FactionName, readonly string[]> = {
  "√ÅGUIA DOURADA": ["Por √Åguia Dourada!", "Voe alto!", "Guardem a cidade!"],
  "SERPENTE NEGRA": ["Deslizem!", "As sombras vencem!", "Mordida certeira!"],
  "LOBO DE FERRO": ["Uivem!", "A√ßo e coragem!", "Protejam o cl√£!"],
  "CORVO ESCARLATE": ["Escarlate eterno!", "Corvos no c√©u!", "Ningu√©m passa!"]
};

export function getFactionChants(faction: FactionName) {
  return FACTION_CHANTS[faction] ?? [];
}

export function getRandomFaction(): FactionName {
  const index = Math.floor(Math.random() * FACTIONS.length);
  return FACTIONS[index];
}

export function pickFactionTeams(): FactionWarTeams {
  const shuffled = [...FACTIONS].sort(() => Math.random() - 0.5);
  return {
    teamA: shuffled.slice(0, 2),
    teamB: shuffled.slice(2, 4)
  };
}

export function isOpposingFaction(
  targetFaction: FactionName | undefined,
  sourceFaction: FactionName | undefined,
  warState?: FactionWarTeams | null
) {
  if (!targetFaction || !sourceFaction || !warState) return false;
  const { teamA, teamB } = warState;
  const sourceTeam = teamA.includes(sourceFaction) ? "A" : teamB.includes(sourceFaction) ? "B" : null;
  const targetTeam = teamA.includes(targetFaction) ? "A" : teamB.includes(targetFaction) ? "B" : null;
  if (!sourceTeam || !targetTeam) return false;
  return sourceTeam !== targetTeam;
}
